export default {
    data() {
        return {
			nickname:""
            // title: 'input',
            // focus: false,
            // inputValue: '',
            // changeValue: ''
        }
    },
    methods: {
		getdata(){
			uni.getStorage({
				key: 'rename',
				// data: res.tempFilePaths,
				success:res=>{
					this.rename=res.data;
				},
				fail: res => {
					this.rename="蓝泽邀请你修改名字"
				}
			});	
		},
        // onKeyInput: function(event) {
        //     this.inputValue = event.target.value
        // },
		submit(){
			var data=this.nickname;
			// console.log(data,"6666666s")
			uni.showModal({
				content:"是否确认修改?",
				// mask:true,
				showCancel:true,
				success:res=>{
					console.log(res);
					if (res.confirm) {
						console.log(this.nickname,"ggggggggggggggggggggggg"),
						uni.setStorage({
							key: 'rename',
							data,
							success: function () {
								uni.navigateTo({
									url:"/pages/user/setting/setting"
									// animationDuration:1000
								})
							}
						});								
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
				
			})
		}
		
	},
	onReady(){
		this.getdata()
	},
	onShow(){
		this.getdata()
	},
	onLoad(){
		this.getdata()
	}
}
